<script setup>
import { ref } from "vue";
import { VueFlow, useVueFlow } from "@vue-flow/core";
import { Background } from "@vue-flow/background";
import { ControlButton, Controls } from "@vue-flow/controls";
import { MiniMap } from "@vue-flow/minimap";
import { initialEdges, initialNodes,Plugins,LargeModel } from "../initial-elements.js";
import ToolIcon from "./ToolIcon.vue";
import ToolBox from "./ToolBox.vue";
import WorkFlowData from "./WorkFlowData.vue";
import LargeModelNode from "./Node/LargeModelNode.vue";
import PluginsNode from "./Node/PluginsNode.vue";
import StartNode from "./Node/StartNode.vue";
import EndNode from "./Node/EndNode.vue";
import SelectorNode from "./Node/SelectorNode.vue";
import KnowledgeBaseNode from "./Node/KnowledgeBaseNode.vue";

const {
  onInit,
  onNodeDragStop,
  onConnect,
  addEdges,
  setViewport,
  toObject,
  //onNodesChange,
  applyNodeChanges,
  //onEdgesChange,
  applyEdgeChanges,
} = useVueFlow();

onInit((vueFlowInstance) => {
  vueFlowInstance.fitView();
});

onNodeDragStop(({ event, nodes, node }) => {
  workflow.nodes = toObject().nodes;
});

onConnect((connection) => {
  addEdges(connection);
});

const onNodesChange = async (changes) => {
  const nextChanges = [];
  for (const change of changes) {
    if (change.type === "remove") {
      var node = workflow.nodes.filter((item) => item.id === change.id);
      if (node.length > 0) {
        if (node[0].type === "Start") {
          alert("开始节点不能删除");
          continue;
        } else if (node[0].type === "End") {
          alert("结束节点不能删除");
          continue;
        } else {
          nextChanges.push(change);
        }
      }
    } else {
      nextChanges.push(change);
    }
  }
  applyNodeChanges(nextChanges);
};

const onEdgesChange = async (changes) => {
  const nextChanges = [];
  for (const change of changes) {
    if (change.type === "remove") {
      if (workflow.selectedNode) {
        continue;
      } else {
        nextChanges.push(change);
      }
    } else {
      nextChanges.push(change);
    }
  }
  applyEdgeChanges(nextChanges);
};

function logToObject() {
  console.log(toObject());
}

function resetTransform() {
  setViewport({ x: 0, y: 0, zoom: 1 });
}
</script>
<script>
var workflow = null;
export default {
  name: "WorkFlow",
  props: {
    workflow_data: {
      title: "智能助理",
    },
  },
  components: {
    VueFlow,
    Background,
    MiniMap,
    ToolBox,
    Controls,
    ToolIcon,
    ControlButton,
    WorkFlowData,
    LargeModelNode,
    PluginsNode,
    StartNode,
    EndNode,
    SelectorNode,
  },
  created() {
    let _this = this;
    workflow = _this;
  },
  data() {
    return {
      ShowNodeBox: false,
      workflow: { title: "智能助理" },
      plugins:ref(Plugins),
      largeModel:ref(LargeModel),
      nodes: ref(initialNodes),
      edges: ref(initialEdges),
      dark: ref(false),
      selectedNode: null,
      selectEdge: null,
    };
  },
  methods: {
    onShowNodeBox() {
      this.ShowNodeBox = !this.ShowNodeBox;
    },
    toggleDarkMode() {
      this.dark = !this.dark;
    },
    addNode(nodeObj) {
      console.log(nodeObj);
      this.nodes.push(nodeObj);
    },
    saveNode(nodeObj) {
      this.nodes = this.nodes.map((item) => {
        if (item.id == nodeObj.id) {
          item.data = nodeObj.data;
        }
        return item;
      });
    },
    nodeClick(node) {
      this.selectedNode = node;
      this.selectEdge = null;
    },
    edgeClick(edge) {
      this.selectedNode = null;
      this.selectEdge = edge;
    },
  },
};
</script>

<template>
  <VueFlow
    :nodes="nodes"
    :edges="edges"
    :class="{ dark }"
    class="basic-flow"
    :default-viewport="{ zoom: 1.5 }"
    :min-zoom="0.2"
    :max-zoom="4"
    :apply-default="false"
    fit-view-on-init
    @nodes-change="onNodesChange"
    @edges-change="onEdgesChange"
    @edgeClick="edgeClick"
    @nodeClick="nodeClick"
  >
    <Background pattern-color="#f0f0f0" :gap="15" />
    <WorkFlowData
      class="vue-flow__panel top left"
      :title="workflow_data.title"
    ></WorkFlowData>
    <MiniMap position="bottom-left" class="MiniMap"></MiniMap>
    <Controls position="bottom-left">
      <ControlButton title="Reset Transform" @click="resetTransform">
        <ToolIcon name="reset" />
      </ControlButton>

      <ControlButton title="Toggle Dark Mode" @click="toggleDarkMode">
        <ToolIcon v-if="dark" name="sun" />
        <ToolIcon v-else name="moon" />
      </ControlButton>

      <ControlButton title="Log `toObject`" @click="logToObject">
        <ToolIcon name="log" />
      </ControlButton>
    </Controls>

    <Panel class="ToolBox vue-flow__panel top left vue-flow__controls">
      <ToolBox :is-visible="ShowNodeBox" @addNode="addNode"></ToolBox>
      <ControlButton title="Reset Transform" @click="onShowNodeBox">
        <ToolIcon v-if="!ShowNodeBox" name="zoomin" />
        <ToolIcon v-else name="zoomout" />
      </ControlButton>
    </Panel>

    <template #node-Start="props">
      <StartNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></StartNode>
    </template>
    <template #node-End="props">
      <EndNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></EndNode>
    </template>
    <template #node-LargeModel="props">
      <LargeModelNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :largeModel="largeModel"
        @saveNode="saveNode"
      ></LargeModelNode>
    </template>
<template #node-KnowledgeBase="props">
      <KnowledgeBaseNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :knowledgeBase="KnowledgeBase"
        @saveNode="saveNode"
      ></KnowledgeBaseNode>
    </template>
    <template #node-Plugins="props">
      <PluginsNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :plugins="Plugins"
        @saveNode="saveNode"
      ></PluginsNode>
    </template>
    <template #node-Selector="props">
      <SelectorNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></SelectorNode>
    </template>
  </VueFlow>
</template>
