<template>
  <div v-if="isVisible" class="NodeToolBox">
    <div class="title">选择节点</div>
    <div class="items iconfont">
      <ul>
        <li v-for="(item, i) in NodeList" :key="i" @click="addNode(item.NodeType)">
          <div class="Icon" :class="item.Icon"></div>
          <div class="Name">{{ item.NodeName }}</div>
          <div class="Icon RBt icon-jinrujiantouxiao"></div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script setup></script>

<script>
export default {
  name: "ToolBox",
  props: {
    node: Object,
    isVisible: Boolean,
  },
  data() {
    return {
      NodeList: [
        {
          NodeName: "大模型",
          NodeType: "LargeModel",
          Icon: "icon-LargeModel",
          Description: "调用大语言模型，处理变量，配合提示词，生成输出",
        },
        {
          NodeName: "知识库",
          NodeType: "KnowledgeBase",
          Icon: "icon-KnowledgeBase",
          Description: "根据输入的文本，调用知识库，获取知识库数据",
        },
        {
          NodeName: "插件",
          NodeType: "Plugins",
          Icon: "icon-Plugins",
          Description: "插件可以提供更多能力，外部操作，获取外部数据等",
        },
        {
          NodeName: "选择器",
          NodeType: "Selector",
          Icon: "icon-selector1",
          Description: "可以通过选择器的定义，链接多个下游执行节点",
        },
      ],
    };
  },
  methods: {
    addNode(NodeType) {
      var node = {
        id:'Node_'+ this.$parent.nodes.length+1,
        type: NodeType,
        data: { label: NodeType },
        position: { x: 0, y: 0 },
      };
      this.$emit("addNode", node);
    },
  },
};
</script>
